.cms-media{
    width: 100%;
    height: 309px;
    position: relative;
    margin-bottom: 40px;
}
.cms-media::before{
    content: '';
    width: 100%;
    height: 100%;
    background-color: #2F80ED;
    opacity: 0.5;
    position: absolute;
    left: 0;
    top: 0;
}
.cms-media img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.cms-texts h1, .cms-texts h2, .cms-texts h3, .cms-texts h4, .cms-texts h5, .cms-texts h6{
    font-size: 20px;
    font-weight: 600;
    color: #2B2D42;
    padding-bottom: 10px;
}

.cms-texts p{
    font-size: 16px;
    line-height: 24px;
    color: #636363;
    padding-bottom: 10px;
}

.cms-texts p + h1, .cms-texts p + h2, .cms-texts p +  h3, .cms-texts p + h4, .cms-texts p + h5, .cms-texts p + h6{
    padding-top: 30px;
}